.single-events,
.tax-events_category {

    @include ipad-landscape {

        .qodef-related-events-holder {
            
            padding-bottom: 70px;

            .qodef-related-event {

                .qodef-related-event-info {
                    padding: 36px 0 0 18px;
                }
            }
        }
    
        aside.qodef-sidebar {
            margin-bottom: 80px;
        }
    }

    @include ipad-portrait {

        .qodef-author-description {

            .qodef-author-description-inner {
                padding: 46px 0px 45px 0px;
                min-height: 86px;
            }

            .qodef-author-description-text-holder {
                margin-top: 0;
                width: 75%;
            }
        }

        .qodef-related-events-holder {

            .qodef-related-event {
                float: none;
                width: 100%;

                .qodef-related-event-image {

                    img {
                        width: 151px;
                    }
                }

                .qodef-related-event-info {
                    padding: 36px 0 0 18px;
                }
            }
        }
    }

    @include phone-landscape {

        .tt_event_items_list {

            li.type_info {

                .tt_event_text {
                    margin-left: 0;
                }
            }
        }

        .qodef-author-description {

            .qodef-author-description-inner {
                padding: 46px 23px;
                min-height: 86px;
            }

            .qodef-author-description-text-holder {
                width: 100%;
                padding: 36px 0 0;
            }
        }

        .qodef-related-events-holder {

            .qodef-related-event {
                float: none;
                width: 100%;

                .qodef-related-event-image {
                    float: none;
                    width: 100%;

                    img {
                        width: 100%;
                    }
                }

                .qodef-related-event-info {
                    float: none;
                    width: 100%;
                    padding: 18px 28px;
                    box-sizing: border-box;
                }
            }
        }
    }
}